.empty_content{
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: var(--theme-3);
      flex-direction: column;
      z-index: 10;
}

.empty_content h2{
      font-size: 6rem;
      color: var(--light);
}

.empty_content p{
      max-width: 40rem;
      text-align: center;
      font-size: 2rem;
      margin-top: 2rem;
      color: var(--light);
}