.data_top__row{
      align-items: center;
      margin-top: 1rem;
}

.data_top__row:first-child{
      margin-top: 0;
}

.data_top__row span{
      display: flex;
      align-items: center;
      gap: 1rem;
      height: 1.6rem;
}

.data_top__row span button{
      padding: .4rem 1rem;
      background: var(--theme-2);
      color: var(--light);
      border-radius: 999px;
      font-size: 1.2rem;
}

.data_top__row span a{
      width: 2.4rem;
      height: 2.4rem;
      background: var(--theme-3);
      color: var(--light);
      border-radius: 50%;
}

.data_top__row span a *{
      font-size: 1.4rem;
}

.data_top__row.location *{
      color: var(--theme-3);
      font-size: 2rem;
}

.data_top__row.location > span{
      color: var(--text-100);
      padding-left: .5rem;
}

.user_data__bottom{
      margin-top: 2rem;
}

.user_data__bottom p{
      white-space: pre-line;
}