.relation_content{
      position: fixed;
      width: 100%;
      height: 100%;
      min-height: 100vh;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.6);
      opacity: 0;
      visibility: hidden;
      transition: .5s ease-in-out;
}

.relation_content.active{
      opacity: 1;
      visibility: visible;
}

.relation_group{
      width: 63.2rem;
      height: 42.6rem;
      background: var(--light);
      position: absolute;
      left: calc(50% - 31.11rem);
      top: 50%;
      transform: translateY(250%);
      padding-top: .5rem;
      transition: .3s .4s ease-in-out;
}

.relation_group:before{
      width: 100%;
      height: .5rem;
      background: var(--v-gradient);
      top: 0;
      left: 0;
}

.relation_content.active .relation_group{
      transform: translateY(-50%);
}

.relation_avatar{
      width: 50%;
      height: 100%;
      flex-shrink: 0;
      position: relative;
}

.relation_avatar__back{
      width: 100%;
      height: 100%;
      overflow: hidden;
      position: absolute;
      top: 0;
      left: 0;
      filter: grayscale(0);
}

.relation_avatar__front{
      position: relative;
      padding: 2.5rem;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.4);
}

.avatar_front__title{
      flex-direction: column;
      color: var(--light);
      border-bottom: solid .1rem rgba(255,255,255,.3);
      position: absolute;
      left: 2.5rem;
      bottom: 3rem;
      width: calc(100% - 5rem);
}

.avatar_front__title h1{
      font-size: 3.4rem;
}

.avatar_front__title strong{
      font-size: 2.4rem;
}

.relation_data{
      width: 50%;
      padding: 1.5rem;
}

.relation_data__header{
      align-items: center;
      gap: .5rem;
}

.relation_data__header *{
      font-size: 3rem;
      color: var(--theme-2);
}

.relation_data__header h3{
      font-size: 2.4rem;
      color: var(--theme-3);
}

.relation_data__description{
      margin-top: 1rem;
}

.relation_data__description p{
      white-space: pre-line;
}

.relation_data__navigator{
      margin-top: 2rem;
      align-items: center;
      gap: 1rem;
}

.relation_data__navigator a{
      width: 9rem;
      height: 3rem;
      text-align: center;
      background: var(--v-gradient);
      color: var(--light);
      border-radius: 999px;
      line-height: 2.75rem;
      font-size: 1.4rem;
}

.close_modal{
      position: absolute;
      bottom: 0;
      right: 0;
      width: 4.4rem;
      height: 4.4rem;
      background: var(--theme-2);
      color: var(--light);
}

.close_modal *{
      font-size: 3rem;
}