.friends_content{
      position: relative;
      width: 100%;
      height: auto;
      padding: 2.5rem;
      align-items: center;
}

.friends_group{
      width: 41.5rem;
      height: 12.6rem;
      position: relative;
      margin-left: 1.5rem;
}

.friends_group::before,
.friends_group::after{
      width: 6rem;
      height: 100%;
      top: 0;
      z-index: 2;
}

.friends_group:before{
      background: linear-gradient(90deg, var(--bg-200), transparent);
      left: 0;
}

.friends_group:after{
      background: linear-gradient(-90deg, var(--bg-200), transparent);
      right: 0;
}

.friend_row{
      width: 10.35rem;
      height: 12.6rem;
      padding: 0 .75rem;
}

.friend_box{
      width: 100%;
      height: 100%;
      flex-direction: column;
      align-items: center;
      gap: 1rem;
}

.friend_box__avatar{
      width: 9.6rem;
      height: 9.6rem;
      background: var(--v-gradient);
      border-radius: 50%;
      position: relative;
      padding: .8;
}

.friend_box__avatar_img{
      width: 8rem;
      height: 8rem;
      background: var(--light);
      border-radius: 50%;
      box-shadow: 0 0 0 .3rem var(--light);
      overflow: hidden;
}

.friend_btn__link{
      position: absolute;
      width: 8.2rem;
      height: 8.2rem;
      background: var(--light);
      border-radius: 50%;
      color: var(--light);
      opacity: 0;
      transition: .3s ease-in-out;
}

.friend_box__avatar:hover .friend_btn__link{
      opacity: .75;
}

.friend_box > span{
      width: 9.2rem;
      height: 2.4rem;
      background: var(--v-gradient);
      text-align: center;
      color: var(--light);
      border-radius: 999px;
      line-height: 2.2rem;
      font-size: 1.4rem;
}

.slick-arrow{
      display: none;
      opacity: 0;visibility: hidden;
}

.slick-dots{
      bottom: -4rem;
}

.slick-dots li button:before{
      background: var(--theme-2);
      width: 2rem;
      height: .6rem;
      border-radius: 999px;
      color: transparent;
      opacity: 1;
}

.slick-dots li.slick-active button:before{
      opacity: 1;
      color: transparent;
      background: var(--theme-3);
}