.aside_content{
      width: 64.2rem;
      height: 100vh;
      min-height: 92rem;
      position: fixed;
      top: 0;
      left: 0;
}

.aside_back,
.aside_back__img,
.aside_front{
      position: absolute;
      width: 100%;
      height: 100%;
      background: var(--h-gradient);
      top: 0;
      left: 0;
}

.aside_back:before{
      position: absolute;
      width: 100%;
      height: 100%;
      background: var(--h-gradient);
      top: 0;
      left: 0;
      z-index: 2;
      opacity: .75;
}

.aside_back__img img{
      filter: grayscale(0);
      mix-blend-mode: screen;
}

.aside_front{
      background: none;
      z-index: 3;
}

.aside_front__top{
      width: 100%;
      height: calc(100% - 36.8rem);
      position: relative;
}

.user_data{
      flex-direction: column;
      align-items: center;
      padding-top: 10rem;
}

.user_data__bg{
      width: 46.5rem;
      height: 46.5rem;
      position: absolute;
      top: calc(50% - 23.25rem);
      left: calc(50% - 23.25rem);
}

.bg_shaper{
      width: 33rem;
      height: 33rem;
      background: var(--a-gradient);
      border-radius: 1.5rem;
      position: absolute;
      top: calc(50% - 16.5rem);
      left: calc(50% - 16.5rem);
      transform: rotate(135deg);
      box-shadow: 0 0 30px rgba(0,0,0,.15);
      opacity: .65;
}

.bg_shaper:nth-child(even){
      transform: rotate(315deg);
}

.bg_shaper:nth-child(2){
      width: 28.2rem;
      height: 28.2rem;
      top: calc(50% - 14.1rem);
      left: calc(50% - 14.1rem);
}

.bg_shaper:nth-child(3){
      width: 25rem;
      height: 25rem;
      top: calc(50% - 12.5rem);
      left: calc(50% - 12.5rem);
}

.bg_shaper:nth-child(4){
      width: 22rem;
      height: 22rem;
      top: calc(50% - 11rem);
      left: calc(50% - 11rem);
}

.user_data__avatar{
      width: 21.7rem;
      height: 21.7rem;
      background: var(--h-gradient);
      position: relative;
      box-shadow: 0 0 50px rgba(0,0,0,.3);
      border-radius: 1.5rem;
}

.user_data__avatar_img{
      width: 20.8rem;
      height: 20.8rem;
      border-radius: 1.2rem;
      overflow: hidden;
}

.user_data__social{
      align-items: center;
      justify-content: center;
      gap: 1rem;
      margin-top: 3rem;
      position: relative;
      z-index: 3;
}

.aside_front__bottom{
      width: 100%;
      height: 36.8rem;
      background-image: url(../../assets/img/wave_bg__front.svg);
      background-size: cover;
      align-items: center;
      justify-content: center;
}

.user_infor{
      flex-direction: column;
      align-items: center;
      margin-top: 8rem;
}

.user_infor__title{
      flex-direction: column;
      align-items: center;
}

.user_infor__title h1{
      font-size: 4rem;
}

.user_infor__title p{
      text-align: center;
}

.user_info__nav{
      width: 30rem;
      margin-top: 4rem;
      height: auto;
      justify-content: center;
      gap: 1rem;
      position: relative;
}

.user_info__nav:before{
      width: 100%;
      height: 2.4rem;
      background: var(--h-gradient);
      border-radius: 999px;
      top: -3.4rem;
      left: 0;
}

.user_info__nav a{
      width: 8rem;
      height: 2.4rem;
      background: var(--v-gradient);
      text-align: center;
      line-height: 2.4rem;
      color: var(--light);
      border-radius: 999px;
      font-size: 1.4rem;
}