.header_content{
      width: 100%;
      height: auto;
      align-items: center;
      position: relative;
}

.header_content:before{
      width: 2.6rem;
      height: .8rem;
      background: var(--theme-2);
      border-radius: 999px;
      right: 0;
      top: calc(50% - .4rem);
      box-shadow: -3.6rem 0 var(--theme-2);
}

.header_info{
      align-content: center;
      gap: 1.5rem;
}

.header_icon{
      width: 4.6rem;
      height: 4.6rem;
      position: relative;
}

.header_icon:before{
      width: 100%;
      height: 100%;
      background: var(--h-gradient);
      border-radius: 46px 15px 46px 46px;
      transform: rotate(45deg);
}

.header_icon__item{
      width: 3.6rem;
      height: 3.6rem;
      position: relative;
      border-radius: 50%;
      overflow: hidden;
      background: var(--light);
}

.header_icon__item *{
      font-size: 2.4rem;
      color: var(--theme-3);
}

.header_data span{
      display: block;
      position: relative;
      padding-left: 1.4rem;
}

.header_data span:before{
      width: 1rem;
      height: 1rem;
      background: var(--theme-2);
      border-radius: 50%;
      left: 0;
      top: calc(50% - .5rem);
}