.content{
      width: calc(100% - 64.2rem);
      height: auto;
      position: relative;
      transform: translateX(64.2rem);
      padding-left: 8rem;
}

.section_content{
      position: relative;
      width: 100%;
      height: auto;
      display: inline-block;
      margin-top: 5rem;
}

.section_content:before{
      width: 4.5rem;
      height: 16.5rem;
      background: var(--h-gradient);
      border-radius: 1.5rem 0 0 1.5rem;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
}

.content_row{
      width: calc(100% - 7.5rem);
      max-width: 60rem;
      min-width: 52.6rem;
      height: auto;
      padding: 2rem;
      background: var(--bg-200);
      border-radius: 1.5rem;
      box-shadow: 2rem 2rem 30px rgba(0,0,0,.08);
}

.content_row__base{
      width: calc(100% - 2.3rem);
      height: auto;
      margin-top: 2rem;
      padding: 2rem 0 2rem 4rem;
      position: relative;
}

.content_row__base:before,
.content_row__base:after{
      width: .1rem;
      height: calc(50% - 1rem);
      background: var(--border);
      left: 2.3rem;
}

.content_row__base:before{
      top: 0;
}

.content_row__base:after{
      bottom: 0;
}

.base_dot{
      width: 1rem;
      height: 1rem;
      position: absolute;
      background: var(--theme-2);
      border-radius: 50%;
      top: calc(50% - .5rem);
      left: 1.8rem;
}